body {
  box-sizing: border-box;
  color: #000000;
  padding: 0 !important;
  font-size: 23px;
  line-height: 1.8;
  transition: .6s all;
  position: relative;
  font-weight: bold;
  font-family: 'Yu Gothic';
  background: #a8ddda;
  overflow: hidden;
}

body.over-hidd {
  transition: .6s all;
  overflow: unset;
}

body h1, body h2, body h3, body h4, body h5, body h6, body p, body label, body ul, body li, body .card, body a {
  text-decoration: none !important;
  transition: .3s all;
  margin-bottom: 0;
  outline: none !important;
}

body a {
  transition: .3s all !important;
  color: #000000;
}

body ul {
  padding: 0;
  margin: 0;
}

body input, body button {
