}

.content-form .item label span {
  font-size: 12px;
  color: #c62634;
  /* padding-left: 18px; */
  border: 1px solid red;
  color: red;
  display: inline-block;
  padding: 1px 7px;
  line-height: 1.4;
  border-radius: 4px;
  margin-left: 14px;
}

.content-form .item .inp-right {
  flex: 0 0 calc(100% - 190px);
}

.content-form .item input[type="text"] {
  width: 100%;
  height: 50px;
  padding: 0 15px;
  text-align: left;
  font-size: 16px;
  font-weight: bold;
  border: 1px solid #ccc;
}

.content-form .item select {
  width: 100%;
  height: 50px;
  padding: 10px 15px;
  text-align: center;
  font-size: 16px;
  font-weight: bold;
  border: 0;
  outline: none;
  -moz-appearance: none;
  /* Firefox */
  -webkit-appearance: none;
  /* Safari and Chrome */
  appearance: none;
}

.content-form [type="radio"]:checked,
.content-form [type="radio"]:not(:checked) {
  position: absolute;
  left: -9999px;
}

.content-form [type="radio"]:checked + label,
.content-form [type="radio"]:not(:checked) + label {
  position: relative;
  padding-left: 45px;
  cursor: pointer;
  line-height: 20px;
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
}

.content-form [type="radio"]:checked + label:before,
.content-form [type="radio"]:not(:checked) + label:before {
  content: '';
  position: absolute;
  left: 0;
  top: -7px;
  width: 30px;
  height: 30px;
  border: 1px solid #e7141b;
  border-radius: 100%;
  background: #fff;
}

.content-form [type="radio"]:checked + label:after,
.content-form [type="radio"]:not(:checked) + label:after {
  content: '';
  width: 10px;
  height: 10px;
  background: #e7141b;
  position: absolute;
  top: 3px;
  left: 10px;
  border-radius: 100%;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

.content-form [type="radio"]:not(:checked) + label:after {
  opacity: 0;
  -webkit-transform: scale(0);
  transform: scale(0);
}

.content-form [type="radio"]:checked + label:after {
  opacity: 1;
  -webkit-transform: scale(1);
  transform: scale(1);
}

.content-form .select-choose {
  position: relative;
}

.content-form .select-choose:after {
  content: "\f107";
  font-family: fontawesome;
  position: absolute;
  top: 10px;
  right: 20px;
  font-size: 18px;
}

