.status-time .status-2:after {
  content: "";
  background: #ffc302;
  width: 20px;
  height: 14px;
  position: absolute;
  left: 0;
  top: 4px;
  border-radius: 20px;
}

.btn-popup button {
  min-width: 150px;
  font-size: 14px;
  text-transform: uppercase;
  font-weight: bold;
  height: 50px;
  border-radius: 4px;
  background: transparent linear-gradient(180deg, #FF7D00 0%, #D80539 100%);
  color: #fff;
  border: 0;
  cursor: pointer;
}

.list-ranger-time .item-ranger.fullResver label:after {
  background-color: red;
}

.multi-field-wrapper {
  position: relative;
}

.multi-field-wrapper .add-field {
  position: absolute;
  right: -30px;
  top: 10px;
  border: 0;
  background: transparent;
  font-size: 18px;
  color: #e7141b;
  cursor: pointer;
}

.multi-field {
  position: relative;
}

.multi-field .remove-field {
  position: absolute;
  right: -30px;
  top: 10px;
  border: 0;
  background: transparent;
  font-size: 18px;
  color: #e7141b;
  cursor: pointer;
}

.multi-field:first-child .remove-field {
  display: none;
}
